@import "../../styles/index.scss";

.Button {
  @include borders;
  @include buttons;
  width: 30px;
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;

  &.visible {
    background: #7b7b7b;
    border-width: 1px;
    border-color: black;
  }

  &.red {
    background: red;
  }

  &.value-1{
    color: blue;
  }

  &.value-2{
    color: green;
  }

  &.value-3{
    color: red;
  }

  &.value-4{
    color: purple;
  }

  &.value-5{
    color: maroon;
  }

  &.value-6{
    color: turquoise;
  }

  &.value-7{
    color: black;
  }

  &.value-8{
    color: gray;
  }
}
